     /*text*/
  
  .col-1{
     text-shadow: 0 4px 5px rgba(0,0,0,0.2);
  }
  
  .countdown{
    position: relative;
    font-size: 25px;
    display: flex;
    margin: 0px auto !important;
    justify-content: center;
    text-align: center;
  }
  
  .countdown div{
    padding: 20px;
    border: 1px solid #fff;
    border-radius: 10px;
    margin:5px;
    opacity: 0.7;
    background: #000;
    color: white;
    font-weight: 600;
  }
  
  .countdown div:first-child{
    background: #17a2b8;
  }
  
  @media(max-width:1150px){
    .countdown div:nth-child(4){
      display: none;
    }
  }
  
  @media(max-width:1150px){
    .countdown div:nth-child(4){
      display: block;
    }
  
    /*.countdown{
      margin-left: 20%;
    }*/
  }

  @media(max-width:1080px){
    .countdown div{
      padding: 15px;;
    }

    .countdown{
      font-size: 25px;
    }
  }



  .countdown span{
    display: block;
    font-size: 20px;
  }

  @media(max-width:510px){
    .countdown span{
      font-size: 15px;
    }

    .countdown div{
      padding: 15px;
    }
  }

  @media(max-width:400px){
    .countdown span{
      font-size: 15px;
    }

    .countdown div{
      padding: 8px;
      font-size: 20px;
    }
  }

  @media(max-width:380px){
    .countdown span{
      font-size: 12px;
    }
  }


  
  
  
  
  
  /*waves*/
  
  .wave{
    position: relative;
    width: 120%;
    height: 143px;
    bottom: 0;
    left: -10%;
    background: url("../../../assets/wave.png");
    /* background-position:-300px; */
    background-position:-30px;
    animation: animate 10s linear infinite;
  }
  
  .ctn-1 .wave::before{
    content: '';
    width: 100%;
    height: 143px;
    background: url("../../../assets/wave.png");
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.6;
    animation-delay: -5s;
    background-position:150px;
    /* background-position:500px; */
    animation: animate-reverse 10s linear infinite;
  }
  
  .ctn-1 .wave::after{
    content: '';
    width: 100%;
    height: 143px;
    background: url("../../../assets/wave.png");
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.4;
    /* background-position:250px; */

    animation: animate 15s linear infinite;
  }
  
  @keyframes animate{
    0%
    {
      background-position:0;
    }
  
    100%
    {
      background-position:1360px;
    }
  }
  @keyframes animate-reverse{
    0%
    {
      background-position:1360px;
    }
  
    100%
    {
      background-position:0px;
    }
  }
  
  
  
  
  
  
